import React from 'react'
import {
  Li,
  Menu,
  MenuContainer,
  ColumnLeft,
  ColumnRight,
  Desc,
  SectionImg,
  ProductName,
  ProductPriceBox,
  ProductPrice,
} from './MenuElements'
import styled from 'styled-components'
import img1 from '../../images/menu/Hamburguesa_Doblecarne.jpg'
import img2 from '../../images/menu/Perro.jpg'

const MenuHeading = styled.h1`
  font-size: clamp(2rem, 2.5vw, 3rem);
  text-align: center;
  padding-top: 6rem;
  background: #e51212;
`

const Combos = ({ heading, isReversed, data1, data2 }) => {
  return (
    <Menu id='combos'>
      <MenuHeading> {heading} </MenuHeading>
      <MenuContainer>
        <ColumnLeft>
          <SectionImg
            src={img1}
            alt='Hamburguesa Doblecarne en combo de Campo 13'
          />
        </ColumnLeft>

        <ColumnRight>
          {data1.map((product, index) => {
            return (
              <Desc key={index}>
                <Li>
                  {' '}
                  <ProductName> {product.name} </ProductName> {product.desc}{' '}
                </Li>
                <ProductPriceBox>
                  <ProductPrice> {product.price} </ProductPrice>
                </ProductPriceBox>
              </Desc>
            )
          })}
        </ColumnRight>
      </MenuContainer>

      <MenuContainer isReversed={isReversed}>
        <ColumnLeft>
          <SectionImg src={img2} alt='Perro en combo de campo 13' />
        </ColumnLeft>

        <ColumnRight>
          {data2.map((product, index) => {
            return (
              <Desc key={index}>
                <Li isReversed={isReversed}>
                  {' '}
                  <ProductName> {product.name} </ProductName> {product.desc}{' '}
                </Li>
                <ProductPriceBox>
                  <ProductPrice> {product.price} </ProductPrice>
                </ProductPriceBox>
              </Desc>
            )
          })}
        </ColumnRight>
      </MenuContainer>
    </Menu>
  )
}

export default Combos
