import React from 'react'
import {
  Menu,
  MenuHeading,
  MenuContainer,
  ColumnLeft,
  ColumnRight,
  Desc,
  SectionImg,
  ProductName,
  ProductPriceBox,
  ProductPrice,
  Li,
} from './MenuElements'
import img from '../../images/menu/bebidas.jpeg'

const Bebidas = ({ heading, isReversed, data }) => {
  return (
    <Menu id='bebidas'>
      <MenuHeading> {heading} </MenuHeading>
      <MenuContainer isReversed={isReversed}>
        <ColumnLeft>
          <SectionImg src={img} alt='bebidas de campo 13' />
        </ColumnLeft>

        <ColumnRight>
          {data.map((product, index) => {
            return (
              <Desc key={index}>
                <Li isReversed={isReversed}>
                  {' '}
                  <ProductName> {product.name} </ProductName> {product.desc}{' '}
                </Li>
                <ProductPriceBox>
                  <ProductPrice> {product.price} </ProductPrice>
                </ProductPriceBox>
              </Desc>
            )
          })}
        </ColumnRight>
      </MenuContainer>
    </Menu>
  )
}

export default Bebidas
